// 
export const foreignerPassTest = function(s) {
  var isIntChar = RegExp(/^[A-Z]{3}\d{6}(?:0[1-9]|1[021])(?:0[1-9]|[21]\d|3[10])\d{2}$/)
  return isIntChar.test(s)
}


